<template>
  <div>
    <main>
      <h1 class="heading">
        Click to turn
        <span class="on">
          <span>on</span>
        </span>
        <span class="off">
          <span>off</span>
        </span>
      </h1>
      <button type="button" class="btn" @click="start">
        <span>
          <b></b>
          <svg viewBox="-5.5 -5.5 71 71" id="circle">
            <circle cx="30" cy="30" r="30" stroke="white" stroke-width="11" fill="transparent" />
          </svg>
        </span>
      </button>
    </main>
  </div>
</template>

<script>
   export default {
      methods: {
         start(){
            const btn = document.querySelector('.btn');
            const heading = document.querySelector('.heading');

            let active = false;

            const turnOn = () => {
               btn.classList.add('active');
               heading.classList.add('active');
            }

            const turnOff = () => {
               btn.classList.remove('active');
               heading.classList.remove('active');
            }

            const toggleAnimation = () => {
               btn.classList.remove('animating');
               active ? turnOn() : turnOff();
            };

            function clickHandler() {
               active = !active;
               btn.classList.add('animating');
               
               btn.addEventListener('animationend', toggleAnimation);
            }

            btn.addEventListener('click', clickHandler);
         }
      },
   }
</script>

<style lang="css" scoped>

   :root {
   --timing: cubic-bezier(0.645, 0.045, 0.355, 1);
   --primary: #f4f7f5;
   }

   main {
   display: grid;
   place-items: center;
   gap: 1em 0;
   }

   /* Heading effect */
   .heading {
   font-family: "Roboto", sans-serif;
   font-weight: 300;
   display: grid;
   grid: auto / auto auto;
   }

   .heading > span {
   margin-left: 0.25em;
   font-weight: 700;
   }

   .heading span {
   overflow: hidden;
   display: inline-flex;
   transition: 0.6s;
   }
   .heading > span,
   .heading > span.on span {
   transform: translateY(100%);
   grid-area: 1 / 2;
   }
   .heading > span span,
   .heading > span.on {
   transform: translateY(-100%);
   }

   .heading.active span.off,
   .heading.active span.off span,
   .heading:not(.active) span.on,
   .heading:not(.active) span.on span {
   transform: none;
   }

   button {
   --w: 6em;
   --bg: rgb(var(--rgb));
   --bga: rgba(var(--rgb), 0.4); /* Safari fix */
   -webkit-appearance: none;
   border: none;
   font-size: 100%;
   width: var(--w);
   height: var(--w);
   background: var(--bg);
   border-radius: 50%;
   position: relative;
   overflow: hidden;
   cursor: pointer;
   transition: 0.1s transform, 0.3s box-shadow;
   will-change: transform, box-shadow;
   backface-visibility: hidden;
   }

   button > * {
   pointer-events: none;
   }

   button:focus {
   outline: none;
   }

   button:active {
   transform: scale(0.97);
   }

   button:after {
   --bg: rgb(var(--rgb));
   content: "";
   position: absolute;
   top: -15%;
   left: -15%;
   width: 200%;
   height: 200%;
   background: var(--bg);
   border-radius: inherit;
   transform: translate(5%, 5%) scale(0.03);
   pointer-events: none;
   }

   button.animating:after {
   animation: c 0.5s cubic-bezier(0.5, 0, 0.5, 1) backwards;
   }

   button.active,
   button:not(.active):after {
   --rgb: 64, 227, 120;
   }

   button:not(.active),
   button.active:after {
   --rgb: 229, 55, 94;
   }

   @keyframes c {
   to {
      transform: none;
   }
   }

   button span {
   display: inline-block;
   position: relative;
   margin-top: 5px;
   }
   button b {
   --w: 7px;
   overflow: hidden;
   border-radius: var(--w);
   display: grid;
   width: var(--w);
   height: 35px;
   top: 0;
   left: 50%;
   position: absolute;
   transform: translateX(-50%);
   z-index: 3;
   will-change: transform;
   }
   button b:before {
   content: "";
   background: #fff;
   width: 100%;
   height: 100%;
   border-radius: var(--w);
   transform: translateY(-15px);
   transition-timing-function: var(--timing);
   transition: 0.5s;
   }

   button.active b:before {
   transform: translateY(12px);
   transition-delay: 0.27s;
   transition-timing-function: cubic-bezier(0.25, 0.25, 0.25, 1.25);
   }

   button svg {
   --dash: 190;
   stroke-linecap: round;
   stroke-dasharray: var(--dash);
   stroke-dashoffset: var(--dash);
   width: 46px;
   height: 46px;
   transform: scaleX(-1) rotate(-89deg);
   transition: 0.4s;
   transition-timing-function: var(--timing);
   margin: auto;
   position: relative;
   z-index: 1;
   will-change: transform, stroke-dashoffset;
   }
   button:not(.active) svg {
   stroke-dashoffset: 40;
   transform: scaleX(-1) rotate(-52deg);
   transition: 0.5s 0.25s;
   }

   .dribbble-logo {
   width: 18px;
   height: 18px;
   margin-right: 5px;
   }
</style>


